@font-face {
  font-weight: 400;
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/cormorant-garamond-regular.woff2") format("woff2"), url("../fonts/cormorant-garamond-regular.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/cormorant-garamond-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold.woff") format("woff"); }

@font-face {
  font-weight: 400;
  font-family: "Open Sans";
  font-style: italic;
  font-display: swap;
  src: url("../fonts/open-sans-italic.woff2") format("woff2"), url("../fonts/open-sans-italic.woff") format("woff"); }

@font-face {
  font-weight: 400;
  font-family: "Consolas";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/consolas.woff2") format("woff2"), url("../fonts/consolas.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Consolas";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/consolas-bold.woff2") format("woff2"), url("../fonts/consolas-bold.woff") format("woff"); }

html {
  scroll-behavior: smooth;
  height: 100%; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
  font-family: "Arial", sans-serif;
  font-style: normal;
  color: #1d1613;
  background-color: #ffffff;
  height: 100%; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  background-position: 0 0; }

.body-wrapper {
  width: 768px;
  margin: 0 auto; }

.main-header h1 {
  font-size: 48px;
  line-height: 48px;
  font-weight: 700;
  font-family: "Consolas", "Arial", sans-serif;
  margin: 0;
  margin-top: 20px;
  text-align: center;
  color: #0062FF; }

.main-directions {
  margin: 0 auto;
  margin-top: 64px; }

.main-directions img {
  display: block;
  margin: 0 auto; }

.main-directions figure {
  margin: 0 15px; }

.main-directions figcaption {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  margin-top: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-style: italic;
  color: #202326; }

.main-server {
  margin: 0 15px;
  margin-top: 106px; }

.main-server h3 {
  font-size: 28px;
  line-height: 36px;
  font-weight: 400px;
  font-family: "Consolas", "Arial", sans-serif;
  margin: 0;
  margin-right: auto;
  color: #000000; }

.main-server blockquote {
  position: relative;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  margin-left: 58px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: italic; }

.main-server svg {
  position: absolute;
  margin-right: 24px;
  top: 0;
  left: -58px; }

.main-advertising {
  margin-top: 45px;
  padding-top: 34px;
  padding-bottom: 36px;
  background-color: #EDEDED; }

.main-advertising figure {
  font-size: 14px;
  line-height: 18px;
  font-weight: 700;
  font-family: "Arial", "Helvetica", sans-serif;
  width: 610px;
  margin: 0 auto; }

.main-advertising figcaption {
  font-weight: 700; }

.main-advertising a {
  font-weight: 700;
  text-decoration: underline;
  color: #0000FF; }

.main-advertising img {
  display: block;
  margin: 0 auto;
  margin-top: 17px; }

.main-conclusion {
  margin: 0 15px;
  margin-top: 45px; }

.main-conclusion h3 {
  font-size: 28px;
  line-height: 36px;
  font-weight: 400;
  margin: 0;
  font-family: "Consolas", "Arial", sans-serif; }

.main-conclusion p {
  margin: 0;
  margin-top: 26px;
  line-height: 24px; }

.main-form {
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #EDEDED; }

.main-form__rate-wrapper {
  margin: 0 15px; }

.main-form__stars {
  overflow: hidden;
  width: 265px;
  margin: 0 auto; }
